// __tests__/components/exercise-common/NavigationButtons.test.js
import React from 'react';
import { render, fireEvent, act } from '@testing-library/react-native';
import { Platform } from 'react-native';
import NavigationButtons from '../../../src/components/exercise-common/NavigationButtons';

// Mock dependencies for stable functionality
jest.mock('expo-linear-gradient', () => {
    const { View } = require('react-native');
    return {
        LinearGradient: (props) => <View {...props} />,
    };
});

jest.mock('@expo/vector-icons', () => {
    const { Text } = require('react-native');
    return {
        Ionicons: (props) => <Text testID={`icon-${props.name}`}>{props.name}</Text>,
    };
});

// Mock the style file to correctly simulate platform-specific styles
jest.mock('../../../src/components/exercise-common/NavigationButtons/style', () => {
    const { StyleSheet, Platform } = require('react-native');
    
    const createStyles = (primaryColor) =>
        StyleSheet.create({
            container: {},
            buttonsRow: {},
            previousButton: {
                ...Platform.select({
                    ios: { shadowColor: 'mock-shadow-ios' },
                    android: { elevation: 3 },
                }),
            },
            previousText: {},
            nextButtonContainer: {
                ...Platform.select({
                    ios: { shadowColor: 'mock-shadow-ios' },
                    android: { elevation: 6 },
                }),
            },
            nextButton: {},
            nextText: {},
            nextIcon: {},
            disabled: {},
        });
    return createStyles;
});

describe('NavigationButtons', () => {
    const onNextMock = jest.fn();
    const onPreviousMock = jest.fn();

    beforeAll(() => {
        jest.useFakeTimers();
    });

    afterAll(() => {
        jest.useRealTimers();
    });

    afterEach(() => {
        jest.clearAllMocks();
    });

    it('renders both previous and next buttons by default', () => {
        const { getByText } = render(<NavigationButtons onNext={onNextMock} onPrevious={onPreviousMock} />);
        expect(getByText('Précédent')).toBeTruthy();
        expect(getByText('Suivant')).toBeTruthy();
    });

    it('hides the previous button when disablePrevious is true', () => {
        const { queryByText } = render(<NavigationButtons disablePrevious={true} />);
        expect(queryByText('Précédent')).toBeNull();
    });

    it('calls onPrevious when the previous button is pressed', () => {
        const { getByText } = render(<NavigationButtons onPrevious={onPreviousMock} />);
        fireEvent.press(getByText('Précédent'));
        act(() => jest.runAllTimers());
        expect(onPreviousMock).toHaveBeenCalledTimes(1);
    });

    it('calls onNext when the next button is pressed', () => {
        const { getByText } = render(<NavigationButtons onNext={onNextMock} />);
        fireEvent.press(getByText('Suivant'));
        act(() => jest.runAllTimers());
        expect(onNextMock).toHaveBeenCalledTimes(1);
    });

    it('does not call onNext when the next button is disabled', () => {
        const { getByText } = render(<NavigationButtons onNext={onNextMock} disableNext={true} />);
        fireEvent.press(getByText('Suivant'));
        expect(onNextMock).not.toHaveBeenCalled();
    });

    it('displays "Terminer" and a checkmark icon on the next button when isLast is true', () => {
        const { getByText, getByTestId } = render(<NavigationButtons isLast={true} />);
        expect(getByText('Terminer')).toBeTruthy();
        expect(getByTestId('icon-checkmark')).toBeTruthy();
    });

    it('applies iOS styles correctly', () => {
        Platform.OS = 'ios';
        const { getByText } = render(<NavigationButtons />);
        const previousButton = getByText('Précédent').parent;
        const nextButton = getByText('Suivant').parent.parent;

        const prevStyles = Array.isArray(previousButton.props.style) ? previousButton.props.style : [previousButton.props.style];
        const nextStyles = Array.isArray(nextButton.props.style) ? nextButton.props.style : [nextButton.props.style];

        const hasShadowColorPrev = prevStyles.some(style => style && style.shadowColor);
        const hasShadowColorNext = nextStyles.some(style => style && style.shadowColor);
        const hasElevationPrev = prevStyles.some(style => style && style.elevation);
        const hasElevationNext = nextStyles.some(style => style && style.elevation);

        expect(hasShadowColorPrev).toBe(true);
        expect(hasShadowColorNext).toBe(true);
        expect(hasElevationPrev).toBe(false);
        expect(hasElevationNext).toBe(false);
    });

    it('applies Android styles correctly', () => {
        Platform.OS = 'android';
        const { getByText } = render(<NavigationButtons />);
        const previousButton = getByText('Précédent').parent;
        const nextButton = getByText('Suivant').parent.parent;

        const prevStyles = Array.isArray(previousButton.props.style) ? previousButton.props.style : [previousButton.props.style];
        const nextStyles = Array.isArray(nextButton.props.style) ? nextButton.props.style : [nextButton.props.style];

        const hasElevationPrev = prevStyles.some(style => style && style.elevation);
        const hasElevationNext = nextStyles.some(style => style && style.elevation);
        const hasShadowColorPrev = prevStyles.some(style => style && style.shadowColor);
        const hasShadowColorNext = nextStyles.some(style => style && style.shadowColor);

        expect(hasElevationPrev).toBe(true);
        expect(hasElevationNext).toBe(true);
        expect(hasShadowColorPrev).toBe(false);
        expect(hasShadowColorNext).toBe(false);
    });
});